'use strict';
import { FireAlarm} from './alarm'

export function NewFireAlarm () {
    var state = {
        name: "fire Alarm",
        temperatureCondition: 100,
        isSmoke: true
    };

    this.withName = function (name) {
        state.name = name;
        return this;
    }

    this.withMaxTemperatureCondition = function (maxTemperature){
        state.maxTemperature = maxTemperature;
        return this;
    }

    this.withSmokeCondition = function (isSmoke){
        state.isSmoke = isSmoke;
        return this;
    }

    this.build = function () {
        return new FireAlarm(state.name, state.temperatureCondition, state.isSmoke);
    }
};
